/*COLOR PALETTE
PRIMARY BROWN: #8D6E63;
SECONDARY TEAL: #009688;
*/

/*HEAD*/
.header-container {
  z-index: 5;
  position: fixed;
  top: 0px;
  left: 0px;
  display: inline-block;
  width: 100%;
  background-color: #EEEEEE !important;
  min-width: 350px;
}

.header-subcontainer {
  display: -ms-flexbox;
  display: flex;
}

.header-subcontainer p {
  margin-left: 1rem;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #9E9E9E;
}

.header-edit-icon {
  opacity: 0;
  color: #757575 !important;
  cursor: pointer;
}

.header-edit-dialog {
  text-align: center;
}

.header-subcontainer p:hover .header-edit-icon {
  opacity: 1;
}

.header-trackcontrol {
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: left !important;
  margin: 0.5rem auto 0rem auto;
  padding: 0rem 0rem 0rem 0rem;
  height: 3.5rem;
}

.header-trackcontrol-icon {
  -webkit-transform: translateY(0.7rem);
      -ms-transform: translateY(0.7rem);
          transform: translateY(0.7rem);
  color: #757575 !important;
  font-size: 250% !important;
}

.header-trackcontrol-icon:hover {
  color: #5D4037 !important;
  cursor: pointer;
}

.header-trackcontrol-icon:active {
  color: #BCAAA4 !important;
}

.header-avatar {
  /*transform: translate(-10px,-5px);*/
}

.badge {
  /*transform: translateY(10px);*/
  /*padding-bottom: 0px !important;
  padding-left: 0px !important;*/
}

.badge span {
  top: 22px !important;
  right: 20px !important;
  width: 10px !important;
  height: 10px !important;
}

.online span {
  background-color: #76FF03 !important;
}

.offline span {
  background-color: #D50000 !important;
}

.header-bottombar {
  -ms-flex-align: end;
      align-items: flex-end;
  display: -ms-flexbox;
  display: flex;
  height: 1rem;
  margin-top: 1rem;
}

.header-addtrack-input {
  margin: 0rem 1rem 0rem 1rem;
  min-width: 14rem;
  -webkit-transform: translateY(0.5rem);
      -ms-transform: translateY(0.5rem);
          transform: translateY(0.5rem);
}

#header-addtrack-input::-webkit-input-placeholder {
  font-size: 12px !important;
  font-family: 'Open Sans', sans-serif;
}

#header-addtrack-input:-ms-input-placeholder {
  font-size: 12px !important;
  font-family: 'Open Sans', sans-serif;
}

#header-addtrack-input::placeholder {
  font-size: 12px !important;
  font-family: 'Open Sans', sans-serif;
}

.header-track-measure {
  border-top-left-radius: 1rem !important;
  /*margin-left: 16rem;*/
  width: 100%;
  height: 2rem;
  background-color: #E0E0E0;
  overflow: hidden;
}

.header-track-measure, .measure-cell, .floating-seek-icon {
  cursor: default;
  -ms-user-select: none;
      user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}

.measure-cell {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 0.8rem;
  padding-left: 5px;
  color: #BCAAA4;
  border-right: 1px solid #BCAAA4;
  width: 40px;
  height: 100%;
  display: inline-block;
}

.thick {
  border-right: 2px solid #A1887F;
}

#measureBarSub {
    overflow-x: scroll;
    overflow-y: hidden;
    height: 100%;
    position: relative;
    white-space: nowrap;
}

/*BODY*/
.body-padding {
  height: 7.5rem;
  width: 100%;
}

.body-container {
  height: 100%;
  width: 100%;
}

.button-addtrack {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

.floating-seek-icon {
  z-index: 9;
  color: #5D4037 !important;
  font-size: 50px !important;
  background-color: rgba(255, 255, 255, 0) !important;
  position: absolute;
  left: 14.5rem;
  -webkit-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
  top: 4.1rem !important;
}

.floating-seek-bar {
  z-index: 2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  width: 2px;
  height: 100%;
  left: 16rem;
  background-color: #5D4037;
}

.tooltip-icon-button {
  position: relative;
  -webkit-transform: translate(-40px,-15px);
      -ms-transform: translate(-40px,-15px);
          transform: translate(-40px,-15px);
}

#markerhead {
  color: #79BED9 !important;
}



